.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

.App-logo-sm {
  height: 6vmin;
  pointer-events: none; 
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #2b3428;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(9px + 1.75vmin);
  color: rgb(34, 136, 119);
}

.Text-blue {
  color: #61fb8f;
}

.App-link {
  cursor: pointer;
  color: #9efb61;
}

.App-link-white {
  cursor: pointer;
  color: #fff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
}

/* Style the search field */
div.search input[type=text] {
  padding: 10px;
  font-size: calc(10px + 2vmin);
  color: #61fbe1;
  border: 1px solid #61fbcd;
  float: left;
  width: 70%;
  background: #282c34;
}

/* Style the submit button */
div.search button {
  float: left;
  width: 25%;
  padding: 10px;
  background: #61fbcd;
  color: rgb(255, 255, 255);
  font-size: calc(10px + 2vmin);
  border: 1px solid #282c34;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
}

div.search button:hover {
  background: #74d14f;
}

/* Clear floats */
div.search::after {
  content: "";
  clear: both;
  display: table;
}

.transactions {
  font-family: "Lucida Console", Courier, monospace;
  border-collapse: collapse;
  width: 100%;
}

.transactions td, .transactions th {
  border: 1px solid #282c34;
  padding: 8px;
}

.transactions tr:hover {background-color: #363b45;}

.transactions th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #4fd181;
  color: white;
}
